.overlay {
  background: linear-gradient(5.13deg, #253339 4.12%, rgba(58, 71, 77, 0) 90.02%);
  position: absolute;
  top: auto;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 30%);
  z-index: z(default);
  //opacity: 0;

  @include bp(phone) {
    opacity: 0.5;
  }

  @include bp(tablet) {
    opacity: 0.5;
  }
}
