.header {
  width: 100%;

  &__wrap {
    margin-left: 60px;
    margin-right: 60px;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: space-between;

    @include bp(phone) {
      margin-left: 24px;
      margin-right: 24px;
    }

    @include bp(tablet) {
      margin-left: 32px;
      margin-right: 32px;
    }
  }

  &__cta {
    width: 100%;
    max-width: 240px;

    @include bp(phone) {
      width: auto;
      max-width: none;
    }

    @include bp(tablet) {
      max-width: 190px;
    }

    @include bp(tablet-lg) {
      width: auto;
      max-width: none;
    }
  }
}

.logo {

  @include bp(phone) {
    width: 31px;
    height: 28px;
  }
}

.cta-link {
  font-weight: 800;
  font-size: 14px;
  line-height: 22px;

  letter-spacing: 0.01em;
  text-transform: uppercase;

  color: #FFFFFF;

  text-decoration: none;
  border-bottom: 0;
  transition: $transition-type $transition-time $transition-rule;

  &:hover {
    color: #ff543e;
  }

  @include bp(phone) {
    font-size: 11px;
    line-height: 11px;

    .with-icon__symbol {
      width: 16px;
      height: 16px;
    }
  }
}

