:root {
  --background: #3B4A51;
  --white: #ffffff;
  --light: #f5f9fa;
  --black: #1f1f1f;
  --text: #FFFFFF;
  --accent: #ffffff;
  --accent-opacity: rgba(255, 255, 255, 0.35);
  --link: #0078ff;
  --link-underline: rgba(0, 120, 255, 0.35);
  --link-hover: #ff0000;
  --link-hover-underline: rgba(255, 0, 0, 0.35);
  --link-active: #cc0000;
  --link-active-underline: rgba(204, 0, 0, 0.35);
}

@font-face {
  font-display: swap;
  font-family: "Object Sans";
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Object-Sans/ObjectSans-Regular.woff2") format("woff2"), url("../fonts/Object-Sans/ObjectSans-Regular.woff") format("woff");
}

@font-face {
  font-display: swap;
  font-family: "Object Sans";
  font-style: normal;
  font-weight: 800;
  src: url("../fonts/Object-Sans/ObjectSans-Heavy.woff2") format("woff2"), url("../fonts/Object-Sans/ObjectSans-Heavy.woff") format("woff");
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-size-adjust: none;
}

html,
body {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  background-color: var(--background);
  color: var(--text);
  display: flex;
  flex-direction: column;
  font-family: "Object Sans", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.4286;
  margin: 0;
  padding: 0;
  transition: all 0.15s ease;
}

html {
  color-scheme: light dark;
}

main {
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

:focus {
  outline: var(--base-focus);
  outline-offset: 4px;
  -moz-outline-radius: 10px;
  transition: outline-offset 0.1s ease;
}

.using-mouse :focus {
  outline: none;
}

