.use {
  margin-left: 80px;
  margin-top: 60px;
}

@media only screen and (max-width: 575px) {
  .use {
    margin-left: 24px;
    margin-top: 0;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .use {
    margin-left: 0;
    margin-top: 0;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .use {
    margin-left: 0;
    margin-top: 0;
  }
}

.use-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  align-content: flex-start;
}

.use__item {
  width: 100%;
  border-bottom: 0;
  text-decoration: none;
  color: #ffffff;
  margin-bottom: 6px;
}

.use__item:last-child {
  margin-bottom: 0;
}

.use__title {
  font-weight: 800;
  font-size: 88px;
  line-height: 88px;
  text-transform: uppercase;
  color: #FFFFFF;
  border-bottom: 0;
  text-decoration: none;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

@media only screen and (max-width: 575px) {
  .use__title {
    font-size: 28px;
    font-size: 9vw;
    line-height: 1;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .use__title {
    font-size: 64px;
    line-height: 64px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .use__title {
    font-size: 64px;
    line-height: 64px;
  }
}

.use__title:hover {
  color: #ff543e;
}

.use__title.youtube:hover {
  color: #ffffff;
}

.use__title.youtube {
  color: #ff543e;
}

.use__title.is-open {
  color: #ff543e;
}

.use__title.is-open.youtube {
  color: #ffffff;
}

.use__body {
  display: none;
}

.use__content {
  padding-top: 6px;
  padding-bottom: 64px;
  font-size: 16px;
  line-height: 26px;
}

.use__content p {
  margin: 0;
}

.use__content .link {
  text-decoration: none;
  border-bottom: 0;
  transition: all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.use__content .link .with-icon__text {
  color: #ffffff;
  border-bottom: 1px solid rgba(255, 255, 255, 0.35);
}

.use__content .link:hover .with-icon__text {
  color: #FF543E;
  border-bottom-color: rgba(255, 84, 62, 0.35);
}

.use__content .link:active .with-icon__text {
  color: var(--link-active);
  border-bottom-color: var(--link-active-underline);
}

.overlay {
  background: linear-gradient(5.13deg, #253339 4.12%, rgba(58, 71, 77, 0) 90.02%);
  position: absolute;
  top: auto;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 30%);
  z-index: 0;
}

@media only screen and (max-width: 575px) {
  .overlay {
    opacity: 0.5;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .overlay {
    opacity: 0.5;
  }
}

.page {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

.page__header {
  margin-top: 60px;
}

@media only screen and (max-width: 575px) {
  .page__header {
    margin-top: 24px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .page__header {
    margin-top: 78px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .page__header {
    margin-top: 78px;
  }
}

.page__use {
  position: absolute;
}

@media only screen and (max-width: 575px) {
  .page__use {
    top: 50%;
    transform: translateY(-50%);
  }
}

@media only screen and (min-width: 768px) and (max-width: 991px) {
  .page__use {
    bottom: 250px;
    left: 32px;
  }
}

@media only screen and (min-width: 992px) and (max-width: 1199px) {
  .page__use {
    bottom: 160px;
    left: 48px;
  }
}

.page__languages {
  position: absolute;
  left: 0;
  bottom: 0;
}

.page__about {
  position: absolute;
  right: 0;
  bottom: 0;
}

.preloader {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: #3B4A51;
  z-index: 100;
}

.preloader__body {
  height: 100%;
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
}

@media only screen and (max-width: 575px) {
  .preloader__body #aa {
    padding-left: 40px;
    padding-right: 40px;
  }
}
