.with-icon {
  $root: &;

  display: inline-flex;
  align-items: center;
  align-content: center;

  #{$root}__symbol + #{$root}__text {
    margin-left: 16px;

    @include bp(phone) {
      margin-left: 8px;
    }
  }

  #{$root}__text + #{$root}__symbol {
    margin-left: 16px;

    @include bp(phone) {
      margin-left: 8px;
    }
  }

  &__symbol {
    line-height: 1;

    img, svg {
      vertical-align: bottom;
    }
  }

  &__text {

  }
}
