.grid-row {
  display: flex;
  align-items: stretch;
  align-content: stretch;
}

.grid-col {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  -ms-flex-preferred-size: 0;
  flex-basis: 0;
  max-width: 100%;

  &:first-child {
    max-width: 340px;
    margin-right: 40px;
  }
}
