.preloader {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  background-color: color(background);
  z-index: z(immortal);

  &__body {
    height: 100%;
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;

    @include bp(phone) {

      #aa {
        padding-left: 40px;
        padding-right: 40px;
      }
    }
  }
}
