.anton {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: z(under);

  &__picture {
    position: absolute;
    right: 157px;
    bottom: -40px;
    opacity: 0;
    transition: opacity 0.6s ease;

    @include bp(phone) {
      left: 0;
      right: 0;
      bottom: -20px;
    }

    @include bp(tablet) {
      height: 100%;
      left: unset;
      right: -73px;
      bottom: -21px;
      display: flex;
      align-items: flex-end;
    }

    @include bp(tablet-lg) {
      right: 53px;
    }
  }

  &__media {
    vertical-align: bottom;

    @include bp(phone) {
      width: 110%;
      height: 100%;
      left: 43px;
      top: 17px;
      position: relative;
    }

    @include bp(tablet) {
      width: 635px;
      //height: 110%;
    }
  }
}
