.use {
  margin-left: 80px;
  margin-top: 60px;

  @include bp(phone) {
    margin-left: 24px;
    margin-top: 0;
  }

  @include bp(tablet) {
    margin-left: 0;
    margin-top: 0;
  }

  @include bp(tablet-lg) {
    margin-left: 0;
    margin-top: 0;
  }

  &-list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    align-content: flex-start;
  }

  &__item {
    width: 100%;
    border-bottom: 0;
    text-decoration: none;
    color: #ffffff;
    margin-bottom: 6px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  // заголовок аккордеона
  &__title {
    font-weight: 800;
    font-size: 88px;
    line-height: 88px;
    text-transform: uppercase;
    color: #FFFFFF;
    border-bottom: 0;
    text-decoration: none;
    cursor: pointer;
    transition: $transition-type $transition-time $transition-rule;

    @include bp(phone) {
      //line-height: 28px;
      font-size: 28px;
      font-size: 9vw;
      line-height: 1;
    }

    @include bp(tablet) {
      font-size: 64px;
      line-height: 64px;
    }

    @include bp(tablet-lg) {
      font-size: 64px;
      line-height: 64px;
    }

    &:hover {
      color: #ff543e;
    }
    &.youtube:hover {
      color: #ffffff;
    }
    
    &.youtube {
      color: #ff543e;
    }

    &.is-open {
      color: #ff543e;
    }

    &.is-open.youtube {
      color: #ffffff;
    }
  }

  &__body {
    display: none;
  }

  &__content {
    padding-top: 6px;
    padding-bottom: 64px;
    font-size: 16px;
    line-height: 26px;

    p {
      margin: 0;
    }

    .link {
      text-decoration: none;
      border-bottom: 0;
      transition: $transition-type $transition-time $transition-rule;

      .with-icon__text {
        color: #ffffff;
        border-bottom: 1px solid transparentize(#ffffff, 1 - $color-opacity);
      }

      &:hover .with-icon__text {
        color: #FF543E;
        border-bottom-color: transparentize(#FF543E, 1 - $color-opacity);
      }

      &:active .with-icon__text {
        color: var(--link-active);
        border-bottom-color: var(--link-active-underline);
      }
    }
  }
}
