$global-font-path : '../fonts/';
$font-path : $global-font-path + 'Object-Sans';
$font-name : 'Object Sans';

@font-face {
  font-display: swap;
  font-family: $font-name;
  font-style: normal;
  font-weight: normal;
  src:
    url('#{$font-path}/ObjectSans-Regular.woff2') format('woff2'),
    url('#{$font-path}/ObjectSans-Regular.woff') format('woff');
}

@font-face {
  font-display: swap;
  font-family: $font-name;
  font-style: normal;
  font-weight: 800;
  src:
    url('#{$font-path}/ObjectSans-Heavy.woff2') format('woff2'),
    url('#{$font-path}/ObjectSans-Heavy.woff') format('woff');
}
