.page {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: z(wrapper);
  overflow: hidden;

  // шапка
  &__header {
    margin-top: 60px;

    @include bp(phone) {
      margin-top: 24px;
    }

    @include bp(tablet) {
      margin-top: 78px;
    }

    @include bp(tablet-lg) {
      margin-top: 78px;
    }
  }

  // аккордеон
  &__use {
    position: absolute;

    @include bp(phone) {
      //margin-top: -24px;
      top: 50%;
      transform: translateY(-50%);
    }

    @include bp(tablet) {
      bottom: 250px;
      left: 32px;
    }

    @include bp(tablet-lg) {
      bottom: 160px;
      left: 48px;
    }
  }

  // переключалка языков
  &__languages {
    position: absolute;
    left: 0;
    bottom: 0;
  }

  // БИО
  &__about {
    position: absolute;
    right: 0;
    bottom: 0;
  }
}
