.about {
  display: flex;
  max-width: 240px;
  margin-right: 60px;
  margin-bottom: 110px;

  @include bp(phone) {
    max-width: 150px;
    margin-right: 24px;
    margin-bottom: 28px;

    hyphens: auto;
  }

  @include bp(tablet-lg) {
    margin-bottom: 78px;
    margin-right: 32px;
  }

  &__caption {
    font-weight: 800;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.01em;
    color: #FFFFFF;
    margin: 0 0 24px 0;

    @include bp(phone) {
      font-size: 13px;
      line-height: 13px;
      margin-bottom: 11px;
    }
  }

  &__who {
    margin: 0;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.01em;
    color: #FFFFFF;

    @include bp(phone) {
      font-size: 11px;
      line-height: 15px;
    }
  }
}
