.languages {
  margin-left: 60px;
  margin-bottom: 30px;
}

// список языков
.languages-list {
  display: flex;
  align-items: center;
  align-content: center;

  margin: 0;
  padding: 0;

  list-style-type: none;

  &__item {

  }

  &__link {
    font-size: 14px;
    line-height: 22px;

    letter-spacing: 0.01em;

    padding: 8px;
    border-bottom: 0;
    text-decoration: none;
    transition: $transition-type $transition-time $transition-rule;
    color: #fff;
    opacity: .05;

    &:hover {
      color: #ff543e;
      opacity: 1;
    }

    &_active {
      opacity: 1;
    }
  }
}
